import { Directive, OnChanges, ElementRef, OnInit, HostListener, AfterViewChecked, DoCheck } from '@angular/core';
import { DotdotdotService } from './dotdotdot.service';

@Directive({
  selector: '[appNgslDotdotdot]'
})
export class NgslDotdotdotDirective implements OnInit, DoCheck {

  constructor(private el: ElementRef, private dotdotdotService: DotdotdotService) { }

  private width = null;
  private height = null;

  private i = 0;

  ngOnInit(): void {
    this.dotdotdotService.setup(this.el.nativeElement);
  }

  ngDoCheck() {
    if (this.width !== this.el.nativeElement.offsetWidth
        || this.height !== this.el.nativeElement.offsetHeight) {
      this.dotdotdotService.setup(this.el.nativeElement);
    }
    this.width = this.el.nativeElement.offsetWidth;
    this.height = this.el.nativeElement.offsetHeight;
  }

  @HostListener('window:resize', ['$event'])
  sizeChange(event) {
    this.dotdotdotService.setup(this.el.nativeElement);
  }
}
