import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ElementRef
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { groups } from 'src/app/constants/groups';
import { SecurityService } from 'src/app/services/security/security.service';

@Component({
  selector: 'app-admin-controls',
  templateUrl: './admin-controls.component.html',
  styleUrls: ['./admin-controls.component.scss']
})
export class AdminControlsComponent implements OnInit {
  public isActivated: Boolean = false;
  public showAlret: Boolean = false;
  public isShow = false;

  @Input() isSimpleConfirm: Boolean = false;

  @Input() canBeDeleted: Boolean = true;
  @Input() canBeEdited: Boolean = true;
  @Input() canBeCreated: Boolean = true;
  @Input() item: any = null;

  @Output() create: EventEmitter<null> = new EventEmitter<null>();
  @Output() edit: EventEmitter<any> = new EventEmitter<any>();
  @Output() remove: EventEmitter<any> = new EventEmitter<any>();

  @Output() activatedControls: EventEmitter<Boolean> = new EventEmitter<Boolean>();

  constructor(
    private route: ActivatedRoute,
    private componentRef: ElementRef,
    private securityService: SecurityService) { }

  ngOnInit() {
    if (this.route.snapshot.data.groups) {
      const g = this.route.snapshot.data.groups;
      if (g && g.includes(groups.administrator)) {
        this.isShow = true;
      }
    } else {
      this.securityService.getCurrentUserGroups()
        .subscribe((g: string[]) => {
          if (g && g.includes(groups.administrator)) {
            this.isShow = true;
          }
        });
    }
  }

  public mouseLeave() {
    this.isActivated = false;
  }

  public onClick(event) {
    if (this.isActivated &&
      !this.componentRef.nativeElement.contains(event.target)) {
      this.closeControls(event);
    }
  }

  public handleCreate(event) {
    event.stopPropagation();
    this.closeControls(event);
    this.create.emit();
  }

  public handleEdit(event) {
    event.stopPropagation();
    this.closeControls(event);
    this.edit.emit(this.item);
  }

  public handleRemove(event) {
    event.stopPropagation();
    this.closeControls(event);
    this.remove.emit(this.item);
  }

  public toggleAlert(event) {
    event.stopPropagation();
    if (this.isSimpleConfirm) {
      if (window.confirm('Вы действительно хотите удалить элемент?')) {
        this.handleRemove(event);
      }
    } else {
      this.showAlret = !this.showAlret;
    }
  }

  public closeControls(event) {
    event.stopPropagation();
    this.showAlret = false;
    this.isActivated = false;
    this.activatedControls.emit(this.isActivated);
  }

  public openControls(event) {
    event.stopPropagation();
    this.showAlret = false;
    this.isActivated = true;
    this.activatedControls.emit(this.isActivated);
  }
}
