import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

import { ActivatedRoute } from '@angular/router';

export interface Comment {
  id: number;
  comment?: string;
  photo?: string;
  name?: string;
  dateTitle?: string;
  authorId?: number;
  authorTitle?: string;
}

@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.scss']
})
export class CommentsComponent implements OnInit {
  @Input() comments: Comment[] = [];
  @Input() isAdmin = false;

  @Output() create: EventEmitter<string> = new EventEmitter<string>();
  @Output() edit: EventEmitter<[number, string]> = new EventEmitter<[number, string]>();
  @Output() delete: EventEmitter<number> = new EventEmitter<number>();

  public createAreaOpened: Boolean = false;
  public textComment: string;
  public commentId: number = null;
  userId: number;

  constructor(private route: ActivatedRoute) {}

  ngOnInit() {
    this.userId = this.route.snapshot.data.userId;
  }

  public toggleCreateArea() {
    this.createAreaOpened = !this.createAreaOpened;
  }

  public setHeight(textArea: HTMLTextAreaElement) {
    textArea.style.height = 'auto';
    textArea.style.height = `${textArea.scrollHeight}px`;
  }

  public createComment() {
    if (this.textComment && this.textComment !== '') {
      this.create.emit(this.textComment);
      this.createAreaOpened = false;
      this.textComment = '';
    }
  }

  public editComment(id: number, text: string) {
    this.edit.emit([id, text]);
  }

  public deleteComment(id: number) {
    this.delete.emit(id);
  }

  public canEdit(comment: Comment) {
    return comment.authorId === this.userId || this.isAdmin;
  }
}
