import {
  Component, AfterContentInit, ContentChildren,
  QueryList, Input, EventEmitter, OnDestroy
} from '@angular/core';
import { FiltersItemComponent } from './filters-item/filters-item.component';
import { FilterEventsService } from './filter-events.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-filters',
  templateUrl: './filters.component.html',
  styleUrls: ['./filters.component.scss']
})
export class FiltersComponent implements AfterContentInit, OnDestroy {

  @ContentChildren(FiltersItemComponent)
  filterItems: QueryList<FiltersItemComponent>;

  @Input() height: String = '100%';
  @Input() reset: EventEmitter<any> = null;
  private resetSubscription: Subscription = null;

  constructor(private filterService: FilterEventsService) { }

  private resetActiveItems() {
    this.filterItems.toArray().forEach(item => {
      item.active = false;
    });
  }

  ngAfterContentInit() {
    this.filterService.changeActiveItem.subscribe(() => {
      this.resetActiveItems();
    });

    const activeFilters = this.filterItems.filter(item => item.active === true);

    if (activeFilters.length === 0) {
      this.filterItems.first.active = true;
    }
    if (this.reset) {
      this.resetSubscription = this.reset.subscribe(() => {
        this.filterItems.forEach(element => {
          element.active = false;
        });
        this.filterItems.first.active = true;
      });
    }
  }

  ngOnDestroy(): void {
    if (this.resetSubscription != null) {
      this.resetSubscription.unsubscribe();
    }
  }
}
