import {
  Component, OnInit, Input, ViewChildren, ElementRef, QueryList, EventEmitter, Output, AfterViewInit
} from '@angular/core';
import { TreeNode, TreeNodeValue } from '../tree-menu.component';

@Component({
  selector: 'app-tree-menu-node',
  templateUrl: './tree-menu-node.component.html',
  styleUrls: ['./tree-menu-node.component.scss']
})
export class TreeMenuNodeComponent implements OnInit, AfterViewInit {
  @ViewChildren('parentElement', { read: ElementRef })
  parentElements: QueryList<ElementRef>;
  @ViewChildren('titleElement', { read: ElementRef })
  titleElements: QueryList<ElementRef>;
  @ViewChildren('recurciveElement', { read: TreeMenuNodeComponent })
  treeElements: QueryList<TreeMenuNodeComponent>;

  parentElement: TreeMenuNodeComponent = null;

  @Output() change = new EventEmitter<TreeNodeValue>();

  // Дефолтное значение, елси на вход нет елементов, в качестве примера
  @Input() trees: TreeNode[] = [];

  constructor() {}

  ngOnInit() {

  }

  ngAfterViewInit() {
    this.treeElements.forEach(element => {
      element.parentElement = this;
    });
  }

  private collapseExpandedChildren(parent: TreeMenuNodeComponent) {
    parent.parentElements.toArray().forEach(element => {
      element.nativeElement.className = 'parent';
    });
    parent.treeElements.toArray().forEach(element => {
      this.collapseExpandedChildren(element);
    });
  }

  public resetActiveElements(parent: TreeMenuNodeComponent) {
    parent.titleElements.toArray().forEach(element => {
      element.nativeElement.className = 'title';
    });
    parent.treeElements.toArray().forEach(element => {
      this.resetActiveElements(element);
    });
  }

  selectItem(node: HTMLElement, item: TreeNodeValue) {
    if (!node.classList.contains('is-current-item')) {
      const root: TreeMenuNodeComponent = this.getRootElement();
      this.resetActiveElements(root);
      node.className += ' is-current-item';
      root.change.emit(item);
    }
  }

  toggleChildren(parent: HTMLElement) {
    if (!parent.classList.contains('is-expanded')) {
      this.collapseExpandedChildren(this);
      parent.className += ' is-expanded';
    } else {
      parent.className = 'parent';
    }
  }

  private getRootElement(): TreeMenuNodeComponent {
    let root: TreeMenuNodeComponent = this;
    while (root.parentElement != null) {
      root = root.parentElement;
    }
    return root;
  }
}
