import { Injectable, EventEmitter, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class SidebarService {

  constructor(@Inject(DOCUMENT) private document: Document) { }

  public setSettings: EventEmitter<object> = new EventEmitter();
  public toggleSidebar: EventEmitter<null> = new EventEmitter();
  public toggleSiteMap: EventEmitter<boolean> = new EventEmitter();
  public toggleFullServices: EventEmitter<boolean> = new EventEmitter();
  public toggleActivity: EventEmitter<boolean> = new EventEmitter();
  public toggleLocation: EventEmitter<boolean> = new EventEmitter();

  private isSiteMapVisible = false;
  private isFullServicesVisible = false;
  private isActivityVisible = false;
  private isLocationVisible = false;

  public handleSetSettings(obj) {
    this.setSettings.emit(obj);
  }

  public handleToggleSidebar() {
    const body = this.document.querySelector('.js-global-wrapper');
    body.classList.toggle('right-sidebar-opened');
    this.toggleSidebar.emit();
  }

  public handleToggleSiteMap() {
    document
      .querySelector('.js-global-wrapper')
      .classList.toggle('is-modal-open');
    this.isSiteMapVisible = !this.isSiteMapVisible;
    this.toggleSiteMap.emit(this.isSiteMapVisible);
  }

  public handleToggleFullServices() {
    document
      .querySelector('.js-global-wrapper')
      .classList.toggle('is-modal-open');
    this.isFullServicesVisible = !this.isFullServicesVisible;
    this.toggleFullServices.emit(this.isFullServicesVisible);
  }

  public handleToggleActivity() {
    this.isActivityVisible = !this.isActivityVisible;
    this.toggleActivity.emit(this.isActivityVisible);
  }

  public handleToggleLocation() {
    this.isLocationVisible = !this.isLocationVisible;
    this.toggleLocation.emit(this.isLocationVisible);
  }
}
