import { Injectable, EventEmitter } from '@angular/core';
import { flatMap, map } from 'rxjs/operators';
import { Observable, of } from 'rxjs';

import { Company } from './company';
import { EmployeesService } from '../employees/employees.service';
import { Employee } from '../employees/employee';
import { ListItemsService } from '../list-items-service';


@Injectable({
  providedIn: 'root'
})
export class CompaniesService extends ListItemsService<Company>   {

  public companyChangeEvent: EventEmitter<Company> = new EventEmitter();

  private company: Observable<Company> = null;

  constructor(private employeesService: EmployeesService) {
    super();
  }

  getListTitle(): string {
    return 'Предприятия';
  }

  getListRelativePath(): string {
    return 'Lists/slCompanies';
  }

  getSelect(): string {
    return 'Id,Title,slCode,slUrl';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return [['Id', true]];
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): Company {
    const entity: Company = {
      id: item.Id,
      title: item.Title,
      code: item.slCode,
      url: item.slUrl
    };
    return entity;
  }

  convertEntityToListItem(entity: Company) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Company[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

  getCompanyForCurrentUser(): Observable<Company> {
    return this.employeesService.getCurrentEmployee().pipe(
      flatMap((e: Employee) => this.getItems({ filter: `slCode eq '${e.company}'`})),
      map((c: Company[]) => c.length > 0 ? c[0] : null));
  }

  getCurrentCompany(): Observable<Company> {
    if (this.company == null) {
      this.company = this.getCompanyForCurrentUser();
      this.company.subscribe((company: Company) => this.companyChangeEvent.emit(company));
    }
    return this.company;
  }

  setCurrentCompany(company: Company) {
    this.company = of(company);
    this.companyChangeEvent.emit(company);
  }
}
