import { Component, OnInit, Inject, ViewChild, ElementRef, AfterViewInit, NgZone } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { fromEvent, merge } from 'rxjs';
import { distinctUntilChanged, map, throttleTime } from 'rxjs/operators';
import { Router, NavigationEnd } from '@angular/router';

import { PortalService } from 'src/app/services/portal/portal.service';
import { CompaniesService } from 'src/app/services/companies/companies.service';
import { Company } from 'src/app/services/companies/company';

@Component({
  selector: 'app-left-menu',
  templateUrl: './left-menu.component.html',
  styleUrls: ['./left-menu.component.scss']
})
export class LeftMenuComponent implements OnInit, AfterViewInit {
  @ViewChild('menuRef') menuRef: ElementRef;

  private enterprise = 'Предприятие';

  public links: any[] = [
    {
      title: 'Главная',
      path: ['/company'],
      icon: 'assets/icons/icon-menu-company.svg',
      active: false
    },
    {
      title: this.enterprise,
      path: ['/enterprise'],
      icon: 'assets/icons/icon-menu-enterprise.svg',
      active: false
    }
    // {
    //   title: 'Личный профиль',
    //   path: '/#',
    //   icon: 'assets/icons/icon-menu-personal.svg',
    //   notifications: 2
    // }
  ];

  private s4 = null;
  private header = null;
  private suiteBarDelta = null;
  private s4Ribbonrow = null;
  public sticky = false;

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private sharepointService: PortalService,
    private zone: NgZone,
    private router: Router,
    private companiesService: CompaniesService
  ) { }

  private onWorkSpaceScroll(scrollTop) {
    const headerHeight = this.header.offsetHeight || 100;
    if (scrollTop >= headerHeight) {
      this.sticky = true;
      this.setOffsetTop();
    } else {
      this.sticky = false;
      this.menuRef.nativeElement.style.top = '0px';
    }
  }

  private setOffsetTop(): void {
    const barHeight = this.suiteBarDelta.offsetHeight;
    const ribbonHeight = this.s4Ribbonrow.offsetHeight;
    this.menuRef.nativeElement.style.top = `${barHeight + ribbonHeight}px`;
  }

  private iniVariables(): void {
    this.s4 = this.document.getElementById('s4-workspace');
    this.header = this.document.getElementById('js-app-header');
    this.suiteBarDelta = this.document.getElementById('suiteBarDelta');
    this.s4Ribbonrow = this.document.getElementById('s4-ribbonrow');
  }

  public getRelativePath() {
    return this.sharepointService.getCurrentUserContext().webServerRelativeUrl;
  }

  ngOnInit() {
    this.companiesService.companyChangeEvent
      .subscribe((c: Company) => {
        const id = c && c.id || null;
        const e = this.links.filter(l => l.title === this.enterprise)[0];
        e.path = ['/enterprise', id];
      });
    this.companiesService.getCurrentCompany();
    this.router.events.forEach((event) => {
        if (event instanceof NavigationEnd) {
          this.zone.run(() => {
            this.setActiveLink(event.url);
          });
        }
      });
  }

  setActiveLink(url: string) {
    this.links.forEach(l => {
      if (l.path && l.path.length > 0 && url.indexOf(l.path[0]) >= 0) {
        l.active = true;
      } else {
        l.active = false;
      }
    });
  }

  ngAfterViewInit() {
    this.iniVariables();
    const events$ = merge(
      fromEvent(this.s4, 'scroll'),
      fromEvent(this.s4, 'wheel')
    );
    const scroll$ = events$.pipe(
      throttleTime(10),
      map(() => this.s4.scrollTop),
      distinctUntilChanged()
    );
    scroll$.subscribe(scrollTop => {
      this.onWorkSpaceScroll(scrollTop);
    });
  }

  goToUrl(event, path) {
    event.stopPropagation();
    this.router.navigate(path);
  }
}
