import {
  Component,
  AfterContentInit,
  ContentChildren,
  QueryList,
  HostListener,
  EventEmitter,
  ElementRef,
  Output,
  ViewChild,
  OnDestroy,
  NgZone
} from '@angular/core';
import { NgslGalleryItemDirective } from './ngsl-gallery-item.directive';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'ngsl-gallery',
  exportAs: 'ngsl-gallery',
  templateUrl: './ngsl-gallery.component.html',
  styleUrls: ['./ngsl-gallery.component.scss']
})
export class NgslGalleryComponent implements AfterContentInit, OnDestroy {
  @Output() toggleGallery: EventEmitter<Boolean> = new EventEmitter();
  @ContentChildren(NgslGalleryItemDirective) items: QueryList<NgslGalleryItemDirective>;
  @ViewChild('containerRef') containerRef: ElementRef;

  public isVisibleGallery: Boolean = false;
  public currentSlide = null;
  public countItems = null;
  private subscribe$ = null;

  public changeSlide(direction, event) {
    event.stopPropagation();
    if (direction && this.countItems - 1 >= this.currentSlide + 1) {
      this.currentSlide++;
    }
    if (!direction && this.currentSlide - 1 >= 0) {
      this.currentSlide--;
    }
  }

  constructor(private zone: NgZone) {}

  @HostListener('document:click', ['$event.target'])
  public onClick(targetElement) {
    this.zone.run(() => {
      if (this.containerRef && !this.containerRef.nativeElement.contains(targetElement)) {
        this.isVisibleGallery = false;
      }
    });
  }

  public closeGallery() {
    this.isVisibleGallery = false;
    this.toggleGallery.emit(this.isVisibleGallery);
  }

  public handleToggleGallery(index, event) {
    event.stopPropagation();
    this.currentSlide = index;
    this.isVisibleGallery = !this.isVisibleGallery;
    this.toggleGallery.emit(this.isVisibleGallery);
  }

  ngAfterContentInit() {
    this.countItems = this.items.length;
    this.subscribe$ = this.items.changes.subscribe(() => {
      this.countItems = this.items.length;
    });
  }

  ngOnDestroy() {
    this.subscribe$.unsubscribe();
  }
}
