module.exports = ".radio-button {\n  display: block;\n  position: relative;\n  font-size: 0;\n  margin-bottom: 10px; }\n  .radio-button input[type=\"radio\"] {\n    display: none; }\n  .radio-button input[type=\"radio\"]:checked + .radio-button-container .inner-circle {\n      -webkit-transform: scale(0.5);\n          -ms-transform: scale(0.5);\n              transform: scale(0.5); }\n  .radio-button .radio-button-container {\n    margin-right: 10px;\n    width: 18px;\n    height: 18px;\n    position: relative; }\n  .radio-button .radio-button-container .outer-circle,\n    .radio-button .radio-button-container .inner-circle {\n      width: 18px;\n      height: 18px;\n      border-radius: 100%;\n      box-sizing: border-box; }\n  .radio-button .radio-button-container .outer-circle {\n      border: 2px solid #A2B2C8;\n      position: absolute;\n      top: 0;\n      left: 0; }\n  .radio-button .radio-button-container .inner-circle {\n      -webkit-transform: scale(0);\n          -ms-transform: scale(0);\n              transform: scale(0);\n      background-color: #dd1e25;\n      transition: -webkit-transform .3s;\n      transition: transform .3s;\n      transition: transform .3s, -webkit-transform .3s; }\n  .radio-button .radio-button-text,\n  .radio-button .radio-button-container {\n    display: inline-block;\n    vertical-align: middle;\n    font-size: 14px;\n    color: #000;\n    cursor: pointer; }\n"