import { Component, OnInit, Input, Inject, ViewChild, ElementRef, NgZone, OnDestroy } from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-user-thumb',
  templateUrl: './user-thumb.component.html',
  styleUrls: ['./user-thumb.component.scss']
})
export class UserThumbComponent implements OnInit, OnDestroy {


  @ViewChild('modalRef') modalRef: ElementRef;

  // Default props
  @Input() photo: string = null;
  @Input() width = '56px';
  @Input() height = '56px';
  @Input() showStatus = true;
  @Input() active = true;
  @Input() route: string[];
  @Input() hasZoom = false;
  @Input() hasModalPhoto = false;
  public isOpenModalPhoto = false;

  private modalSubscription: Subscription = null;

  constructor(@Inject(DOCUMENT) private document: Document, private zone: NgZone) { }

  public togglePhotoModal(event) {
    event.stopPropagation();
    this.isOpenModalPhoto = !this.isOpenModalPhoto;
  }

  ngOnInit() {
    if ( this.hasModalPhoto ) {
      this.modalSubscription = fromEvent(this.document, 'click').subscribe(event => {
        this.zone.run(() => {
          if (this.modalRef && !this.modalRef.nativeElement.contains(event.target)) {
            this.isOpenModalPhoto = false;
          }
        });
      });
    }
    if (this.photo == null) {
      this.photo = this.photo || 'assets/photos/no-foto.svg';
    }
  }

  ngOnDestroy(): void {
      if (this.modalSubscription != null) {
        this.modalSubscription.unsubscribe();
      }
  }
}
