import {
  AfterContentInit,
  OnInit,
  Component,
  ContentChildren,
  Directive,
  ElementRef,
  Input,
  QueryList,
  ViewChild,
  ViewChildren,
  EventEmitter,
  Output,
  ContentChild,
  NgZone,
  OnDestroy
} from '@angular/core';

import { NgslCarouselItemDirective } from './ngsl-carousel-item.directive';
import { NgslCarouselDotDirective } from './ngsl-carousel-dot.directive';
import {
  animate,
  AnimationBuilder,
  AnimationFactory,
  AnimationPlayer,
  style
} from '@angular/animations';
import { NgslCarouselControlDirective } from './ngsl-carousel-control.directive';
import { Observable, Subscription } from 'rxjs';

@Directive({
  // tslint:disable-next-line:directive-selector
  selector: '.ngsl-carousel-item-directive'
})
// tslint:disable-next-line:directive-class-suffix
export class CarouselItemElement { }
@Directive({
  // tslint:disable-next-line:directive-selector
  selector: '.ngsl-carousel-dot-directive'
})
// tslint:disable-next-line:directive-class-suffix
export class CarouselDotElement { }
@Directive({
  // tslint:disable-next-line:directive-selector
  selector: '.ngsl-carousel-control-directive'
})
// tslint:disable-next-line:directive-class-suffix
export class CarouselControlElement { }

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'ngsl-carousel',
  exportAs: 'ngsl-carousel',
  templateUrl: './ngsl-carousel.component.html',
  styleUrls: ['./ngsl-carousel.component.scss']
})
export class NgslCarouselComponent implements AfterContentInit, OnInit, OnDestroy {

  /* carousel start item */
  @ContentChildren(NgslCarouselItemDirective) items: QueryList<
    NgslCarouselItemDirective
  >;
  @ViewChildren(CarouselItemElement, { read: ElementRef })
  private itemsElements: QueryList<ElementRef>;
  @ViewChild('track') private carousel: ElementRef;
  /* carousel end item */

  /* carousel start dot */
  @ContentChildren(NgslCarouselDotDirective) dotsList: QueryList<
    NgslCarouselDotDirective
  >;
  @ViewChildren(CarouselDotElement, { read: ElementRef })
  private dotElements: QueryList<ElementRef>;
  @ViewChild('dotTrack') private dotTrack: ElementRef;
  /* carousel end dot */

  /* carousel start control */
  @ContentChild(NgslCarouselControlDirective) control: NgslCarouselControlDirective;
  /* carousel end control */

  /* Input props start*/
  @Input() timing = 450;
  @Input() rewindTiming = 900;
  @Input() arrows = true;
  @Input() dots = true;
  @Input() infinity = true;
  @Input() dynamicTiming = false;
  @Input() autoPlay = false;
  @Input() autoPlayTiming = 2000;
  @Input() perView = 1;
  @Input() margin = 0;
  @Input() showArrowsLastElement = true;
  @Input() isControl = false;
  @Input() trackIsUp = false;
  @Input() currentSlide = 0;
  @Output() currentSlideChange: EventEmitter<number> = new EventEmitter<number>();
  @Input() prevSlide: number = null;
  @Output() prevSlideChange: EventEmitter<number> = new EventEmitter<number>();
  @Input() nextSlide: number = null;
  @Output() nextSlideChange: EventEmitter<number> = new EventEmitter<number>();

  /* Input props end*/
  private player: AnimationPlayer;
  private action = null;
  private autoPlayId = null;
  private autoPlayPause = false;
  private offsetSlide = 0;
  public animationStyle = 'transform 450ms';
  public transformStyle = 'translate3d(0, 0, 0)';
  private changes: Subscription = null;

  trackWidth = {};

  private buildAnimation(offset) {
    const duration = this.action === 'REWIND' ? this.rewindTiming : this.timing;
    this.animationStyle = `transform ${duration}ms`;
    this.transformStyle = `translate3d(${offset}%, 0, 0)`;
  }

  private changeSlide(action?: string) {
    this.setSlidesIndex();
    const itemW = 100 / this.items.length / this.perView;
    this.offsetSlide = itemW * this.currentSlide * this.perView;
    this.buildAnimation(-this.offsetSlide);
    // console.log(`changeSlide action: ${action}, is current slide: `, this.currentSlide + 1);
  }

  goToSlide(index) {
    if (this.dynamicTiming) {
      // В разработке, тестовая опция
      this.timing = this.timing + 55 * (index - this.currentSlide);
    }
    this.currentSlide = index;
    this.currentSlideChange.emit(this.currentSlide);
    this.action = 'GO_TO_SLIDE';
    this.changeSlide('go to slide');
  }

  next() {
    const countSlides =
      this.perView > 1
        ? this.items.length - this.perView + 1
        : this.items.length;
    if (this.currentSlide + 1 === countSlides && this.infinity) {
      this.currentSlide = 0;
      this.currentSlideChange.emit(this.currentSlide);
      this.action = 'REWIND';
      this.changeSlide('rewind to first slide');
      return;
    }
    if (this.currentSlide + 1 === countSlides && !this.infinity) {
      return;
    }

    this.currentSlide++;
    this.currentSlideChange.emit(this.currentSlide);
    this.action = 'NEXT_SLIDE';
    this.changeSlide('next slide');
  }

  prev() {
    const countSlides =
      this.perView > 1
        ? this.items.length - this.perView + 1
        : this.items.length;
    if (this.currentSlide === 0 && this.infinity) {
      this.currentSlide = countSlides - 1;
      this.currentSlideChange.emit(this.currentSlide);
      this.action = 'REWIND';
      this.changeSlide('rewind to last slide');
      return;
    }
    if (this.currentSlide === 0 && !this.infinity) {
      return;
    }

    this.currentSlide--;
    this.currentSlideChange.emit(this.currentSlide);
    this.action = 'PREV_SLIDE';
    this.changeSlide('prev slide');
  }

  pauseAutoplay(val: boolean) {
    this.autoPlayPause = val;
  }

  private setSlidesIndex() {
    const l = this.items.length;
    if (this.currentSlide && this.currentSlide !== 0) {
      const itemW = 100 / this.items.length / this.perView;
      const offset = itemW * this.currentSlide * this.perView;
      this.buildAnimation(-offset);
    }

    if (this.currentSlide === 0) {
      if (this.infinity) {
        this.prevSlide = l - 1;
      } else {
        this.prevSlide = null;
      }
    } else {
      this.prevSlide = this.currentSlide - 1;
    }
    this.prevSlideChange.emit(this.prevSlide);

    if (this.currentSlide === l - 1) {
      if (this.infinity) {
        this.nextSlide = 0;
      } else {
        this.nextSlide = null;
      }
    } else {
      this.nextSlide = this.currentSlide + 1;
    }
    this.nextSlideChange.emit(this.nextSlide);

  }

  private setTrackWidth() {
    this.trackWidth = {
      width: `${(100 * this.items.length) / this.perView}%`
    };
  }

  private removeDotsInDomElements() {
    const position =
      this.perView > 1 ? this.dotsList.length - this.perView : null;
    this.dotsList.toArray().forEach((dot, i) => {
      if (i > position) {
        dot.isVisible = false;
      }
    });
  }

  private setInitValues() {
    this.setSlidesIndex();
    this.setTrackWidth();

    if (this.autoPlayId) {
      clearInterval(this.autoPlayId);
    }

    if (this.autoPlay && this.items.length > 1) {
      this.autoPlayId = setInterval(() => {
        if (!this.autoPlayPause && !this.trackIsUp) {
          this.next();
        }
      }, this.autoPlayTiming);
    }

    if (this.perView > 1) {
      this.removeDotsInDomElements();
    }
  }

  constructor(private builder: AnimationBuilder) { }

  ngAfterContentInit() {
    this.setInitValues();
    // При изменении данных все пересчитать
    if (this.changes == null) {
      this.changes = this.items.changes.subscribe(() => {
          this.setInitValues();
          if (this.items.length > 0) {
            this.action = 'GO_TO_SLIDE';
            this.changeSlide('go to slide');
          }
      });
    }
  }

  ngOnInit() { }

  ngOnDestroy(): void {
    if (this.changes != null) {
      this.changes.unsubscribe();
    }
  }
}
