import { Pipe, PipeTransform } from '@angular/core';
import { ServiceTemplate } from './service-template';

@Pipe({
  name: 'serviceTempateFilter'
})
export class ServiceTemplateFilterPipe implements PipeTransform {
  transform(items: ServiceTemplate[], searchText: string): any[] {
    if (!items) { return []; }
    if (!searchText) { return items; }
    return items.filter(item => item.title.toLowerCase().indexOf(searchText) >= 0);
  }
}
