import { Component, OnInit, NgZone } from '@angular/core';
import { Router } from '@angular/router';

import { SiteMapService } from 'src/app/services/site-map/site-map.service';
import { SiteMapCategoryService } from 'src/app/services/site-map-category/site-map-category.service';
import { SettingsService } from 'src/app/services/settings/settings.service';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { SiteMap } from 'src/app/services/site-map/site-map';
import { SiteMapCategory } from 'src/app/services/site-map-category/site-map-category';

@Component({
  selector: 'app-site-map',
  templateUrl: './site-map.component.html',
  styleUrls: ['./site-map.component.scss']
})
export class SiteMapComponent implements OnInit {

  constructor(private siteMapService: SiteMapService,
    private siteMapCategoryService: SiteMapCategoryService,
    private settingsService: SettingsService,
    private zone: NgZone,
    private router: Router,
    private sidebarService: SidebarService) { }

  isVisible: Boolean = false;
  public helpLink: string = null;

  public siteMapList: SiteMap[] = [];
  public siteMapCategoriesList: SiteMapCategory[] = [];

  handleToggleSiteMap() {
    this.sidebarService.handleToggleSiteMap();
  }

  ngOnInit() {
    this.siteMapCategoryService.getItems().subscribe(items => this.siteMapCategoriesList = items);
    this.siteMapService.getItems().subscribe(items => this.siteMapList = items);
    this.settingsService.getHelpLink()
      .subscribe((link: string) => this.helpLink = link);

    this.sidebarService.toggleSiteMap.subscribe(isVisible => {
      this.isVisible = isVisible;
    });
  }

  public getHelp() {
    if (this.helpLink != null) {
      this.zone.run(() => {
        if (window.location.href.indexOf('/PortalPages/') > 0
          && this.helpLink.indexOf('/PortalPages/') >= 0
          && this.helpLink.indexOf('#') >= 0) {
          this.router.navigate([this.helpLink.split('#')[1]]);
        } else {
          window.location.href = this.helpLink;
        }
      });
    }
  }

}
