import { Component, OnInit } from '@angular/core';
import { NgZone } from '@angular/core';
import { Router } from '@angular/router';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { PortalService } from 'src/app/services/portal/portal.service';
import { EmployeesService } from 'src/app/services/employees/employees.service';
import { Employee } from 'src/app/services/employees/employee';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  public photo = null;
  public logotype: string = null;
  public route: string[] = [];
  public root: string = null;
  public reason: string = null;
  public isLocationVisible = false;
  public searchIsVisible: Boolean = false;
  public activityFeedIsVisible: Boolean = false;

  constructor(
    private sidebarService: SidebarService,
    private employeesService: EmployeesService,
    private sharepointService: PortalService,
    private zone: NgZone,
    private router: Router
  ) {}

  handleToggleActivityFeed() {
    this.sidebarService.handleToggleActivity();
  }

  handleToggleSearch() {
    this.searchIsVisible = !this.searchIsVisible;
  }

  toggleLocation() {
    this.sidebarService.handleToggleLocation();
  }

  handleToggleSiteMap() {
    this.sidebarService.handleToggleSiteMap();
  }

  handleToggleSidebar() {
    this.sidebarService.handleToggleSidebar();
  }

  handleToggleFullServices() {
    this.sidebarService.handleToggleFullServices();
  }

  ngOnInit() {
    this.root =
      this.sharepointService.getCurrentUserContext().webServerRelativeUrl +
      '/PortalPages/home.aspx#/company';
    this.employeesService
      .getCurrentEmployee()
      .subscribe((employee: Employee) => {
        if (employee != null) {
          this.photo = employee.photo;
          this.route = employee.profileRoute;
          this.reason = employee.reason;
          this.logotype = 'logotype';
        }
      });
    this.sidebarService.toggleActivity.subscribe((isVisible: boolean) =>
      this.zone.run(() => {
        this.activityFeedIsVisible = isVisible;
      })
    );
    this.sidebarService.toggleLocation.subscribe((isVisible: boolean) =>
      this.zone.run(() => {
        this.isLocationVisible = isVisible;
      })
    );
  }

  goToRoot() {
    this.zone.run(() => {
      if (
        window.location.href.indexOf('/PortalPages/') > 0 &&
        this.root.indexOf('#') > 0
      ) {
        this.router.navigate([this.root.split('#')[1]]);
      } else {
        window.location.href = this.root;
      }
    });
  }
}
