import { Component, OnInit, Inject, NgZone } from '@angular/core';
import { RouterOutlet, Router, NavigationEnd, ChildActivationEnd } from '@angular/router';
import { fadeAnimation } from './animations';
import { Title } from '@angular/platform-browser';
import { DOCUMENT } from '@angular/common';
import { filter} from 'rxjs/operators';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { PortalService } from './services/portal/portal.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [fadeAnimation]
})
export class AppComponent implements OnInit {

  public showSidebar: Boolean = false;
  public bgColor: String = '#F7F7F7';

  constructor(private sidebarService: SidebarService,
    @Inject(DOCUMENT) private document: Document,
    private router: Router,
    private zone: NgZone,
    private titleService: Title,
    private sharepointService: PortalService) { }

  prepareRoute(outlet: RouterOutlet) {
    return (
      outlet &&
      outlet.activatedRouteData &&
      outlet.activatedRouteData['animation']
    );
  }

  ngOnInit() {
    this.sidebarService.setSettings.subscribe((obj) => {
      this.showSidebar = obj.showSidebar;
      this.bgColor = obj.bgColor;
    });
    this.sidebarService.toggleSidebar.subscribe(() => {
      this.showSidebar = !this.showSidebar;
    });
    this.router.events.subscribe((evt) => {
      if (evt instanceof NavigationEnd) {
        this.zone.run(() => {
          this.router.navigated = false;
          document.getElementById('s4-workspace').scrollTop = 0;
        });
      }
    });
    const title = this.sharepointService.getCurrentUserContext().webTitle;
    this.router.events.pipe(
      filter(event => event instanceof ChildActivationEnd),
    ).subscribe((e: ChildActivationEnd) => {
      const s = e.snapshot.firstChild || e.snapshot;
      if (s.data['title']) {
        this.zone.run(() => {
          this.titleService.setTitle(title + '. ' + s.data['title']);
        });
      }
    });
  }
}
