import { Component, OnInit, HostListener, ElementRef, ViewChild } from '@angular/core';
import { Router } from '@angular/router';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { Company } from 'src/app/services/companies/company';
import { CompaniesService } from 'src/app/services/companies/companies.service';

@Component({
  selector: 'app-location-picker',
  templateUrl: './location-picker.component.html',
  styleUrls: ['./location-picker.component.scss']
})
export class LocationPickerComponent implements OnInit {

  public selected: Company = null;
  public companies: Company[] = null;

  @ViewChild('locationRef') locationRef: ElementRef;

  constructor(private companiesService: CompaniesService,
    private router: Router,
    private sidebarService: SidebarService) { }

  ngOnInit() {
    this.companiesService.getItems().subscribe(
      (companies: Company[]) => this.companies = companies
    );
    this.companiesService.getCurrentCompany().subscribe(
      (company: Company) => this.selected = company
    );
  }

  public select(company: Company) {
    this.selected = company;
    this.companiesService.setCurrentCompany(company);
    this.router.navigate(['/enterprise', company.id]);
    this.sidebarService.handleToggleLocation();
  }

  @HostListener('document:click', ['$event.target'])
  public onClick(targetElement) {
    if (
      this.locationRef &&
      this.locationRef.nativeElement.contains(targetElement)
    ) {
      this.sidebarService.handleToggleLocation();
    }
  }
}
