import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { flatMap } from 'rxjs/operators';
import * as moment from 'moment';

import { ServiceTemplate } from './service-template';
import { ListItemsService } from '../list-items-service';
import { PortalService } from '../portal/portal.service';

@Injectable({
  providedIn: 'root'
})
export class ServiceTemplatesService extends ListItemsService<ServiceTemplate> {

  constructor(private sharepointService: PortalService) {
    super();
  }

  getListRelativePath(): string {
    return '';
  }

  getListTitle(): string {
    return 'Каталог сервисов';
  }

  getSelect(): string {
    return 'Id,Title,slIndex,slColor,slUrl,slIcon,slHoverIcon,slActive,AttachmentFiles/ServerRelativeUrl,'
      + 'LikedBy/Id,RatedBy/Id,slIsCustom,slIsEnableByDefault,'
      + 'slIsRequired,slCloseDate,slBeginDate,slIsNewWindow';
  }

  getExpand(): string {
    return 'AttachmentFiles,LikedBy,RatedBy';
  }

  getOrderBy(): [string, boolean][] {
    return [['slIndex', true]];
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): ServiceTemplate {
    const serviceTemplate: ServiceTemplate = {
      id: item.Id,
      title: item.Title,
      index: item.slIndex,
      color: item.slColor,
      link: item.slUrl,
      icon: item.slIcon,
      iconHover: item.slHoverIcon,
      isActive: item.slActive,
      isEnableByDefault: item.slIsEnableByDefault,
      isCustom: item.slIsCustom,
      likedByIds: [],
      ratedByIds: [],
      isRequired: item.slIsRequired,
      closeDate: item.slCloseDate && new Date(item.slCloseDate),
      beginDate: item.slBeginDate && new Date(item.slBeginDate),
      isNewWindow: item.slIsNewWindow
    };
    if (item.AttachmentFiles && item.AttachmentFiles.length > 0) {
      serviceTemplate.image = item.AttachmentFiles[0].ServerRelativeUrl;
    }
    if (item.LikedBy) {
      serviceTemplate.likedByIds = item.LikedBy.map(l => l.Id);
    }
    if (item.RatedBy) {
      serviceTemplate.ratedByIds = item.RatedBy.map(l => l.Id);
    }
    return serviceTemplate;
  }

  convertEntityToListItem(entity: ServiceTemplate) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): ServiceTemplate[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

  getVisibleItems(): Observable<ServiceTemplate[]> {
    return this.getItems({ filter: 'slActive eq 1' })
      .pipe(flatMap((items: ServiceTemplate[]) => {
        const today = new Date();
        return of(items.filter(
          i => (i.beginDate == null || i.beginDate <= today) && (i.closeDate == null || i.closeDate >= today)));
      }),
      flatMap((items: ServiceTemplate[]) => {
        const userId = this.sharepointService.getCurrentUserContext().userId;
        const visible: ServiceTemplate[] = items.filter(item => {
          if (item.ratedByIds.indexOf(userId) < 0) {
            return item.isEnableByDefault;
          } else if (item.likedByIds.indexOf(userId) >= 0) {
            return true;
          }
          return false;
        });
        return of(visible);
      }));
  }

  getInvisibleItems(): Observable<ServiceTemplate[]> {
    return this.getItems({ filter: 'slActive eq 1' })
      .pipe(flatMap((items: ServiceTemplate[]) => {
        const today = new Date();
        return of(items.filter(
          i => (i.beginDate == null || i.beginDate <= today) && (i.closeDate == null || i.closeDate >= today)));
      }),
      flatMap((items: ServiceTemplate[]) => {
        const userId = this.sharepointService.getCurrentUserContext().userId;
        const invisible: ServiceTemplate[] = items.filter(item => {
          if (item.ratedByIds.indexOf(userId) < 0) {
            return !item.isEnableByDefault;
          } else if (item.likedByIds.indexOf(userId) < 0) {
            return true;
          }
          return false;
        });
        return of(invisible);
      }));
  }
}
