import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-button-up',
  templateUrl: './button-up.component.html',
  styleUrls: ['./button-up.component.scss']
})
export class ButtonUpComponent implements OnInit, OnDestroy {

  constructor(@Inject(DOCUMENT) private document: Document) {}

  s4 = null;

  isVisible: Boolean = false;

  onWorkSpaceScroll() {
    const offset = this.s4.scrollTop;
    offset >= 350 ? (this.isVisible = true) : (this.isVisible = false);
  }

  scrollTo() {
    const element = this.s4;
    const start = element.scrollTop,
      change = 0 - start,
      increment = 20;

    const easeInOut = function(a, b, c, d) {
      a /= d / 2;
      if (a < 1) {
        return (c / 2) * a * a + b;
      }
      a -= 1;
      return (-c / 2) * (a * (a - 2) - 1) + b;
    };

    const animateScroll = function(elapsedTime) {
      elapsedTime += increment;
      const position = easeInOut(elapsedTime, start, change, 500);
      element.scrollTop = position;
      if (elapsedTime < 500) {
        setTimeout(function() {
          animateScroll(elapsedTime);
        }, increment);
      }
    };

    animateScroll(0);
  }

  ngOnInit() {
    this.s4 = this.document.getElementById('s4-workspace');
    this.s4.addEventListener('scroll', this.onWorkSpaceScroll.bind(this), true);
  }

  ngOnDestroy(): void {
    this.s4.removeEventListener('scroll', this.onWorkSpaceScroll.bind(this), true);
  }
}
