import { Injectable } from '@angular/core';
import * as moment from 'moment';

import { Survey } from './survey';
import { ListItemsService } from '../list-items-service';

@Injectable({
  providedIn: 'root'
})
export class SurveysService extends ListItemsService<Survey> {

  getListRelativePath(): string {
    return '';
  }

  getListTitle(): string {
    return 'Перечень опросов';
  }

  getSelect(): string {
    return 'Id,Title,slNewDate,slEndDate,slMembers/Id,slMembers/Title,'
      + 'slLookupSurveyType/Id,slLookupSurveyType/Title,slLink,Created,AttachmentFiles/ServerRelativeUrl,'
      + 'LikedBy/Id,LikesCount';
  }

  getExpand(): string {
    return 'slLookupSurveyType,slMembers,AttachmentFiles,LikedBy';
  }

  getOrderBy(): [string, boolean][] {
    return [['Created', false]];
  }

  getTop(): number {
    return 10;
  }

  convertListItemToEntity(item: any): Survey {
    const survey: Survey = {
      id: item.Id,
      title: item.Title,
      link: item.slLink,
      image: (item.AttachmentFiles && item.AttachmentFiles.length > 0) ? item.AttachmentFiles[0].ServerRelativeUrl : null,
      newDate: item.slNewDate && new Date(item.slNewDate),
      endDate: item.slEndDate && new Date(item.slEndDate),
      members: [],
      lookupSurveyTypeId: null,
      lookupSurveyTypeTitle: null,
      likesCount: item.LikesCount || 0,
      likedByIds: []
    };
    if (item.slLookupSurveyType) {
      survey.lookupSurveyTypeId = item.slLookupSurveyType.Id;
      survey.lookupSurveyTypeTitle = item.slLookupSurveyType.Title;
    }
    if (item.LikedBy) {
      survey.likedByIds = item.LikedBy.map(l => l.Id);
    }
    return survey;
  }

  convertEntityToListItem(entity: Survey) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Survey[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
