import { Directive, TemplateRef } from '@angular/core';

@Directive({
  // tslint:disable-next-line:directive-selector
  selector: '[ngslGalleryItem]'
})
export class NgslGalleryItemDirective {

  isVisible = false;

  constructor(public tpl: TemplateRef<any> ) { }

  handleOpenGallery() {
    this.isVisible = true;
  }

}
