import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FilterEventsService } from '../filter-events.service';

@Component({
  selector: 'app-filters-item',
  templateUrl: './filters-item.component.html',
  styleUrls: ['./filters-item.component.scss']
})
export class FiltersItemComponent implements OnInit {

  @Input() active: Boolean = false;

  @Output()
  change: EventEmitter<any> = new EventEmitter<any>();

  constructor(private filterService: FilterEventsService) { }

  selectItem() {
    this.filterService.handleChangeActiveItem();
    this.active = true;
    this.change.emit();
  }

  ngOnInit() {
  }

}
