import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';

import { DistributionList } from 'src/app/services/feedback/distribution-list';
import { FeedbackService } from 'src/app/services/feedback/feedback.service';

export interface QuestionForm {
  from: string;
  to: Array<Object>;
  text: string;
}

@Component({
  selector: 'app-feedback-modal-form',
  templateUrl: './feedback-modal-form.component.html',
  styleUrls: ['./feedback-modal-form.component.scss']
})
export class FeedbackModalFormComponent implements OnInit {

  public distributionList: DistributionList[] = [];
  public formValid: Boolean = true;
  public questionForm: QuestionForm = {
    from: null,
    to: null,
    text: null
  };

  constructor(public feedbackService: FeedbackService) {}

  fetchDistributionList() {
    this.feedbackService
      .getDistributionList()
      .then((list: DistributionList[]) => {
        this.distributionList = list;
      });
  }

  toggleModal() {
    this.reset();
    this.feedbackService.toggleModal();
  }

  private reset() {
    this.formValid = true;
    this.questionForm = {
      from: null,
      to: null,
      text: null
    };
  }

  onSubmit(form: NgForm) {
    if ( form.valid && !this.feedbackService.closeDialogIsOpen ) {
      this.formValid = true;
      this.feedbackService.sendMessage(this.questionForm);
      this.toggleModal();
    } else if ( !form.valid ) {
      this.formValid = false;
      return;
    }
  }

  ngOnInit() {
    this.fetchDistributionList();
  }
}
