import { Injectable } from '@angular/core';
import * as moment from 'moment';

import { ListItemsService } from '../list-items-service';
import { SiteMap } from './site-map';

@Injectable({
  providedIn: 'root'
})
export class SiteMapService extends ListItemsService<SiteMap> {

  getListRelativePath(): string {
    return '';
  }

  getListTitle(): string {
    return 'Карта сайта';
  }

  getSelect(): string {
    return 'Id,Title,slIndex,slUrl,slSiteMapCategoriesLookup/Id,slSiteMapCategoriesLookup/Title';
  }

  getExpand(): string {
    return 'slSiteMapCategoriesLookup';
  }

  getOrderBy(): [string, boolean][] {
    return [['slIndex', true]];
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): SiteMap {
    const siteMap: SiteMap = {
      id: item.Id,
      title: item.Title,
      order: item.slIndex,
      url: item.slUrl
    };
    if (item.slSiteMapCategoriesLookup) {
      siteMap.categoryId = item.slSiteMapCategoriesLookup.Id;
      siteMap.category = item.slSiteMapCategoriesLookup.Title;
    }
    return siteMap;
  }
  convertEntityToListItem(entity: SiteMap) {
    throw new Error('Method not implemented.');
  }
  getMockItems(): SiteMap[] {
    return [];
  }
  isMock(): boolean {
    return false;
  }
}
