import { Component, OnInit } from '@angular/core';

import { ImportantInformationsService } from 'src/app/services/important-informations/important-informations.service';
import { ImportantInformation } from 'src/app/services/important-informations/important-information';

@Component({
  // tslint:disable-next-line:component-selector
  selector: '[app-important-information]',
  templateUrl: './important-information.component.html',
  styleUrls: ['./important-information.component.scss']
})
export class ImportantInformationComponent implements OnInit {

  items: ImportantInformation[] = null;

  constructor(private importantInformationsService: ImportantInformationsService) { }

  ngOnInit() {
    this.load();
  }

  load() {
    this.importantInformationsService.getItems({top: 3}).subscribe((items: ImportantInformation[]) => {
      this.items = items;
    });
  }
}
