import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ImagesService {

  constructor() { }

  public prepareBackgroundUrl(link: string) {
    return link && link.replace(/\(/gi, '\\(').replace(/\)/gi, '\\)').replace(/%/gi, '%25').replace(/ /gi, '%20') || null;
  }
}
