import { Injectable } from '@angular/core';
import * as moment from 'moment';

import { UsefullLink } from './usefull-links';
import { ListItemsService } from '../list-items-service';

@Injectable({
  providedIn: 'root'
})
export class UsefullLinksService extends ListItemsService<UsefullLink> {


  getListRelativePath(): string {
    return '';
  }

  getListTitle(): string {
    return 'Полезные ссылки';
  }

  getSelect(): string {
    return 'Id,Title,slUrl,Created';
  }

  getExpand(): string {
    return 'AttachmentFiles';
  }

  getOrderBy(): [string, boolean][] {
    return [['Created', false]];
  }

  getTop(): number {
    return 5;
  }

  convertListItemToEntity(item: any): UsefullLink {
    const usefullLink: UsefullLink = {
      id: item.Id,
      title: item.Title,
      url: item.slUrl
    };
    return usefullLink;
  }

  convertEntityToListItem(entity: UsefullLink) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): UsefullLink[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
