import { Component, OnInit, ViewChild, ElementRef, AfterViewInit, OnDestroy } from '@angular/core';
import { Subscription, fromEvent } from 'rxjs';
import { map } from 'rxjs/operators';

import { ServiceTemplate } from 'src/app/services/service-templates/service-template';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { ServiceTemplatesService } from 'src/app/services/service-templates/service-templates.service';
import { PortalService } from 'src/app/services/portal/portal.service';

@Component({
  selector: 'app-full-services-page',
  templateUrl: './full-services-page.component.html',
  styleUrls: ['./full-services-page.component.scss']
})
export class FullServicesPageComponent implements OnInit, AfterViewInit, OnDestroy {

  @ViewChild('searchRef') searchRef: ElementRef;

  isVisible = false;
  visible: ServiceTemplate[] = null;
  invisible: ServiceTemplate[] = null;
  filter = '';
  subscription: Subscription = null;

  constructor(private sidebarService: SidebarService,
    private serviceTemplatesService: ServiceTemplatesService,
    private sharepointService: PortalService) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
  }

  ngOnInit() {
    this.sidebarService.toggleFullServices
      .subscribe((isVisible: boolean) => this.isVisible = isVisible);
    this.load();
  }

  load() {
    this.serviceTemplatesService.getItems({filter: 'slActive eq 1'})
    .subscribe((items: ServiceTemplate[]) => {
      const userId = this.sharepointService.getCurrentUserContext().userId;
      this.visible = items.filter(item => {
        if (item.ratedByIds.indexOf(userId) < 0) {
          return item.isEnableByDefault;
        } else if (item.likedByIds.indexOf(userId) >= 0) {
          return true;
        }
        return false;
      });
      this.invisible = items.filter(item => {
        if (item.ratedByIds.indexOf(userId) < 0) {
          return !item.isEnableByDefault;
        } else if (item.likedByIds.indexOf(userId) < 0) {
          return true;
        }
        return false;
      });
    });
  }

  ngAfterViewInit(): void {
    if (this.searchRef && this.subscription == null) {
      this.subscription = fromEvent(this.searchRef.nativeElement, 'keyup')
        .pipe(
          map((e: any) => e.target.value),
        ).subscribe((filter: string) => this.filter = filter);
    }
  }

  ngOnDestroy(): void {
    if (this.subscription != null) {
      this.subscription.unsubscribe();
    }
  }

  handleToggleFullServices() {
    this.sidebarService.handleToggleFullServices();
  }

  toggleLike(item: ServiceTemplate, isLike: boolean) {
    const userId = this.sharepointService.getCurrentUserContext().userId;
    if (item.ratedByIds.indexOf(userId) < 0 ) {
      this.serviceTemplatesService.addRating({id: item.id}, 5)
        .subscribe(() => {
          if (item.likedByIds.indexOf(userId) < 0) {
            this.serviceTemplatesService.addLike({id: item.id})
              .subscribe(() => this.load());
          } else {
            this.serviceTemplatesService.removeLike({id: item.id})
              .subscribe(() => this.load());
          }
        });
    } else {
      if (item.likedByIds.indexOf(userId) < 0) {
        this.serviceTemplatesService.addLike({id: item.id})
          .subscribe(() => this.load());
      } else {
        this.serviceTemplatesService.removeLike({id: item.id})
          .subscribe(() => this.load());
      }
    }
  }
}
