import {
  Component, OnInit, Input, ElementRef, EventEmitter, Output, ViewChild
} from '@angular/core';
import { TreeMenuNodeComponent } from './tree-menu-node/tree-menu-node.component';

export interface TreeNodeValue {
  id: number;
  title: string;
  parentId?: number;
}

export interface TreeNode {
  value: TreeNodeValue;
  childrens: TreeNode[];
}

@Component({
  selector: 'app-tree-menu',
  templateUrl: './tree-menu.component.html',
  styleUrls: ['./tree-menu.component.scss']
})
export class TreeMenuComponent implements OnInit {

  @Output() change = new EventEmitter<TreeNodeValue>();

  @ViewChild('treeRootElement', { read: TreeMenuNodeComponent })
  treeElements: TreeMenuNodeComponent;

  // Дефолтное значение, елси на вход нет елементов, в качестве примера
  @Input() items: TreeNodeValue[] = [];
  trees: TreeNode[] = [];

  constructor() {}

  ngOnInit() {
    this.trees = this.getTreeOfArray(this.items);
  }

  public select(item: TreeNodeValue) {
    this.change.emit(item);
  }

  public getTreeOfArray(items: TreeNodeValue[]): TreeNode[]  {
    const nodes: TreeNode[] = [];
    const map = {};
    const roots: TreeNode[] = [];
    for (let i = 0; i < items.length; i += 1) {
      const node: TreeNode = { value: items[i], childrens: [] };
      map[node.value.id] = i;
      nodes.push(node);
    }
    for (let i = 0; i < nodes.length; i += 1) {
      const node = nodes[i];
      if (node.value.parentId != null) {
        nodes[map[node.value.parentId]].childrens.push(node);
      } else {
        roots.push(node);
      }
    }
    return roots;
  }
}
