import { Injectable, Output, EventEmitter } from '@angular/core';

import { DistributionList } from './distribution-list';
import { environment } from 'src/environments/environment';
import { QuestionForm } from 'src/app/core/feedback-modal-form/feedback-modal-form.component';

@Injectable({
  providedIn: 'root'
})
export class FeedbackService {
  modalIsOpen: Boolean = false;
  closeDialogIsOpen: Boolean = false;
  sendingSuccess: Boolean = false;
  loading: Boolean = false;

  // tslint:disable:no-inferrable-types
  private email: string = 'slEmail';

  @Output() emailSendEvent: EventEmitter<Boolean> = new EventEmitter();

  private distributionListTitle = 'Адресаты для вопросов';
  private distributionList: DistributionList[] = [];

  private distributionListTest: DistributionList[] = [];

  public toggleModal() {
    this.modalIsOpen = !this.modalIsOpen;
    this.closeDialogIsOpen = false;
  }

  public toggleModalDialog() {
    this.closeDialogIsOpen = !this.closeDialogIsOpen;
  }

  public getDistributionList() {
    return new Promise((resolve, reject) => {
      if (environment.production === false) {
        this.distributionList = this.distributionListTest;
        resolve(this.distributionList);
      } else {

      }
    });
  }

  public getDistributionListItems(list) {
    this.distributionList = [];
    const results: any[] = list;

    for (const item of results) {
      this.distributionList.push({
        id: item.Id,
        name: item.Title,
        email: item.slEmail
      });
    }
  }

  private sendEmail(from, data) {}

  public sendMessage(data: QuestionForm) {}

  constructor() {}
}
