import { Component, OnChanges, SimpleChanges, Input } from '@angular/core';

import { Employee } from 'src/app/services/employees/employee';
import { EmployeesService } from 'src/app/services/employees/employees.service';

@Component({
  selector: 'app-department-heads',
  templateUrl: './department-heads.component.html',
  styleUrls: ['./department-heads.component.scss']
})
export class DepartmentHeadsComponent implements OnChanges {

  @Input() inline: Boolean = false;
  @Input() employee: Employee;

  manager: Employee = null;
  deputy: Employee = null;

  constructor(private employeesService: EmployeesService) { }

  ngOnChanges(changes: SimpleChanges) {
    const employee: Employee = changes.employee.currentValue;
    if (employee) {
      if (employee.lookupManagerId) {
        this.employeesService.getItem({id: employee.lookupManagerId})
          .subscribe((manager: Employee) => this.manager = manager);
      } else {
        this.manager = null;
      }
      if (employee.lookupDeputyId) {
        this.employeesService.getItem({id: employee.lookupDeputyId})
          .subscribe((deputy: Employee) => this.deputy = deputy);
      } else {
        this.deputy = null;
      }
    }
  }
}
