import { Observable, from, of } from 'rxjs';

export interface IEntity {
  id: number;
}

export interface IListItemsPage<T> {
  hasNext: boolean;
  items: T[];
  getNext(): Observable<IListItemsPage<T>>;
}

export interface IListItemsServiceParams {
  id?: number;
  select?: string;
  expand?: string;
  filter?: string;
  orderBy?: [string, boolean][];
  top?: number;
  skip?: number;
  webId?: string;
}

export interface IAttachment {
  fileName: string;
  serverRelativeUrl: string;
}

export abstract class ListItemsService<T extends IEntity> {


  abstract getListTitle(): string;
  abstract getListRelativePath(): string;
  abstract getSelect(): string;
  abstract getExpand(): string;
  abstract getOrderBy(): [string, boolean][];
  abstract getTop(): number;
  abstract convertListItemToEntity(item: any): T;
  abstract convertEntityToListItem(entity: T): any;
  abstract getMockItems(): T[];
  abstract isMock(): boolean;

  public getItemsPaged(params?: IListItemsServiceParams): Observable<IListItemsPage<T>> {
    return of({items: [], hasNext: false, getNext: () => null});
  }

  public getItems(params?: IListItemsServiceParams): Observable<T[]> {
    return of([]);
  }

  public getItem(params: IListItemsServiceParams): Observable<T> {
    return null;
  }

  public getItemAttachments(params: IListItemsServiceParams): Observable<IAttachment[]> {
    return of([]);
  }

  public createItem(entity: T, webId?: string): Observable<T> {
    return of(entity);
  }

  public updateItem(entity: T, webId?: string): Observable<T> {
    return of(entity);
  }

  public deleteItem(entity: T, webId?: string): Observable<void> {
    return of();
  }

  public recycleItem(entity: T, webId?: string): Observable<void> {
    return of();
  }

  public addLike(params: IListItemsServiceParams): Observable<void> {
    return of();
  }

  public removeLike(params: IListItemsServiceParams): Observable<void> {
    return of();
  }

  public addRating(params: IListItemsServiceParams, rating: number): Observable<void> {
    return of();
  }

  public getLikedItemsByUserId(id: number, webId?: string): Observable<T[]> {
    return of([]);
  }


  public getLikedItemsByUserIdPaged(id: number, webId?: string): Observable<IListItemsPage<T>> {
    return of({items: [], hasNext: false, getNext: () => null});
  }

  public getRandomItems(params: IListItemsServiceParams): Observable<T[]> {
    return of([]);
  }

}
