import { Component, OnInit, Input, HostListener, NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { ImagesService } from 'src/app/services/images/images.service';
import { SurveysService } from 'src/app/services/surveys/surveys.service';
import { PortalService } from 'src/app/services/portal/portal.service';


@Component({
  selector: 'app-tile-polls',
  templateUrl: './tile-polls.component.html',
  styleUrls: ['./tile-polls.component.scss']
})
export class TilePollsComponent implements OnInit {

  public count: number = null;

  @Input() service;

  isHover: Boolean = false;

  @HostListener('mouseover') onHover() {
    this.isHover = true;
  }

  @HostListener('mouseout') onMouseOut() {
    this.isHover = false;
  }

  constructor(private zone: NgZone,
    private imagesService: ImagesService,
    private router: Router,
    private surveysService: SurveysService,
    private sharepointService: PortalService) {}

  ngOnInit() {
    const userId = this.sharepointService.getCurrentUserContext().userId;
  }

  goToUrl(event, link: string) {
    event.stopPropagation();
    this.zone.run(() => {
      if (window.location.href.indexOf('/PortalPages/') > 0
        && link.indexOf('/PortalPages/') >= 0
        && link.indexOf('#') >= 0) {
        this.router.navigate([link.split('#')[1]]);
      } else {
        window.location.href = link;
      }
    });
  }

  public prepareLink(link: string) {
    return this.imagesService.prepareBackgroundUrl(link);
  }
}
