import { Component, AfterContentInit, ContentChildren, QueryList, Input } from '@angular/core';
import { RadioButtonComponent } from './radio-button/radio-button.component';

@Component({
  selector: 'app-radio-group',
  templateUrl: './radio-group.component.html',
  styleUrls: ['./radio-group.component.scss']
})
export class RadioGroupComponent implements AfterContentInit {

  @ContentChildren(RadioButtonComponent) radioButtons: QueryList<RadioButtonComponent>;

  @Input() name: String = 'default-name';

  constructor() {}

  ngAfterContentInit() {
    this.radioButtons.toArray().forEach((element, index) => {
      element.id = `radio-button-${index}`;
      element.groupName = `radio-group-${this.name}`;
    });
  }
}
