import { Component, OnInit } from '@angular/core';

import { UsefullLink } from 'src/app/services/usefull-links/usefull-links';
import { UsefullLinksService } from 'src/app/services/usefull-links/usefull-links.service';

@Component({
  selector: '[app-usefull-links]',
  templateUrl: './usefull-links.component.html',
  styleUrls: ['./usefull-links.component.scss']
})
export class UsefullLinksComponent implements OnInit {

  items: UsefullLink[] = null;

  constructor(private usefullLinksService: UsefullLinksService) { }

  ngOnInit() {
    this.load();
  }

  load() {
    this.usefullLinksService.getItems({top: 5}).subscribe(
      (items: UsefullLink[]) => {
        this.items = items;
      }
    );
  }

}
