import { Component, OnInit, NgZone } from '@angular/core';

import { ServiceTemplate } from 'src/app/services/service-templates/service-template';
import { ServiceTemplatesService } from 'src/app/services/service-templates/service-templates.service';
import { PortalService } from 'src/app/services/portal/portal.service';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';


@Component({
  selector: 'app-portal-services',
  templateUrl: './portal-services.component.html',
  styleUrls: ['./portal-services.component.scss']
})
export class PortalServicesComponent implements OnInit {
  serviceList = [
    {
      id: 1,
      title: 'База знаний',
      icon: 'assets/icons/icon-service-black.svg',
      image: 'assets/banners/banner-tile.png',
      iconHover: 'assets/icons/icon-service-white.svg',
      link: ''
    },
    {
      id: 2,
      title: 'Зарплатный проект',
      icon: 'assets/icons/icon-service-black.svg',
      image: 'assets/banners/banner-tile.png',
      iconHover: 'assets/icons/icon-service-white.svg',
      link: ''
    },
    {
      id: 3,
      title: 'Дни рождения',
      icon: 'assets/icons/icon-service-black.svg',
      image: 'assets/banners/banner-tile.png',
      iconHover: 'assets/icons/icon-service-white.svg',
      link: this.sharepointService.getCurrentUserContext().webServerRelativeUrl
        + '/PortalPages/home.aspx#/birthdays'
    },
    {
      id: 4,
      title: 'Статистика и показатели',
      icon: 'assets/icons/icon-service-black.svg',
      image: 'assets/banners/banner-tile.png',
      iconHover: 'assets/icons/icon-service-white.svg',
      link: ''
    },
    {
      id: 5,
      title: 'Опросы',
      icon: 'assets/icons/icon-service-black.svg',
      image: 'assets/banners/banner-tile.png',
      iconHover: 'assets/icons/icon-service-white.svg',
      link: this.sharepointService.getCurrentUserContext().webServerRelativeUrl
        + '/PortalPages/home.aspx#/polls'
    },
    {
      id: 6,
      title: 'Внутренние вакансии',
      icon: 'assets/icons/icon-service-black.svg',
      image: 'assets/banners/banner-tile.png',
      iconHover: 'assets/icons/icon-service-white.svg',
      link: ''
    },
    {
      id: 7,
      title: 'Tелефонный справочник',
      icon: 'assets/icons/icon-service-black.svg',
      image: 'assets/banners/banner-tile.png',
      iconHover: 'assets/icons/icon-service-white.svg',
      link: this.sharepointService.getCurrentUserContext().webServerRelativeUrl
        + '/PortalPages/home.aspx#/phonebook'
    }
  ];

  public services: ServiceTemplate[];

  constructor(
    private serviceTemplatesService: ServiceTemplatesService,
    private sharepointService: PortalService,
    private zone: NgZone,
    private sidebarService: SidebarService) {}

  ngOnInit() {
    this.serviceTemplatesService.getVisibleItems()
      .subscribe((services: ServiceTemplate[]) => this.services = services);
    this.sidebarService.toggleFullServices.subscribe((isVisible: boolean) => {
      if (!isVisible) {
        this.serviceTemplatesService.getVisibleItems()
          .subscribe((services: ServiceTemplate[]) => this.zone.run(() => { this.services = services; }));
      }
    });
  }
}
