import { Component, Input, HostListener, NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceTemplate } from 'src/app/services/service-templates/service-template';
import { ImagesService } from 'src/app/services/images/images.service';


@Component({
  selector: 'app-tile',
  templateUrl: './tile.component.html',
  styleUrls: ['./tile.component.scss']
})
export class TileComponent {

  @Input() service: ServiceTemplate;

  isHover: Boolean = false;

  @HostListener('mouseover') onHover() {
    this.isHover = true;
  }

  @HostListener('mouseout') onMouseOut() {
    this.isHover = false;
  }

  constructor(
    private zone: NgZone,
    private router: Router,
    private imagesService: ImagesService) {}

  public goToUrl(event, service: ServiceTemplate) {
    event.stopPropagation();
    if (service.isNewWindow) {
      window.open(service.link, '_blank');
    } else {
      this.zone.run(() => {
        const link = service.link;
        if (window.location.href.indexOf('/PortalPages/') > 0
          && link.indexOf('/PortalPages/') >= 0
          && link.indexOf('#') >= 0) {
          this.router.navigate([link.split('#')[1]]);
        } else {
          window.location.href = link;
        }
      });
    }
  }

  public prepareLink(link: string) {
    return this.imagesService.prepareBackgroundUrl(link);
  }
}
