import { Injectable } from '@angular/core';
import * as moment from 'moment';

import { SiteMapCategory } from './site-map-category';
import { ListItemsService } from '../list-items-service';

@Injectable({
  providedIn: 'root'
})
export class SiteMapCategoryService extends ListItemsService<SiteMapCategory> {

  getListTitle(): string {
    return 'Разделы карты сайта';
  }

  getListRelativePath(): string {
    throw new Error('Method not implemented.');
  }

  getSelect(): string {
    return 'Id,Title,AttachmentFiles/ServerRelativeUrl,slIndex';
  }

  getExpand(): string {
    return 'AttachmentFiles';
  }

  getOrderBy(): [string, boolean][] {
    return [['slIndex', true]];
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): SiteMapCategory {
    const siteMapCategory: SiteMapCategory = {
      id: item.Id,
      title: item.Title,
      icon: (item.AttachmentFiles && item.AttachmentFiles.length > 0)
        ? item.AttachmentFiles[0].ServerRelativeUrl : null,
    };
    return siteMapCategory;
  }

  convertEntityToListItem(entity: SiteMapCategory) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): SiteMapCategory[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
