import { Injectable } from '@angular/core';
import { ImportantInformation } from './important-information';
import { ListItemsService } from '../list-items-service';
import * as moment from 'moment';
import { PortalService } from '../portal/portal.service';

@Injectable({
  providedIn: 'root'
})
export class ImportantInformationsService extends ListItemsService<ImportantInformation> {

  constructor(private sharepointService: PortalService) {
    super();
  }

  getListTitle(): string {
    return 'Важная информация';
  }
  getListRelativePath(): string {
    return 'lists/slImportantInformations';
  }

  getSelect(): string {
    return 'Id,Title,slIndex,slDocumentUrl,slImageUrl,Created';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return [['slIndex', true], ['Created', false]];
  }

  getTop(): number {
    return 3;
  }

  convertListItemToEntity(item: any): ImportantInformation {
    const entity: ImportantInformation = {
      id: item.Id,
      title: item.Title,
      documentUrl: item.slDocumentUrl,
      imageUrl: item.slImageUrl,
      index: item.slIndex
    };
    return entity;
  }

  convertEntityToListItem(entity: ImportantInformation) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): ImportantInformation[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

}
